function chartRender(el,data,categories) {
    var options = {
        series: [{
            name: 'series1',
            data: data
        }],
        colors: ['#487BFF', '#4627FF'],
        fill: {
            colors: ['#487BFF', '#4627FF'],
        },
        chart: {
            height: "320",
            type: 'area',
            toolbar: {
                show: false,
            }
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            curve: 'smooth',
            width: 2,
        },
        xaxis: {
            type: 'datetime',
            categories: categories,
            labels: {
                style: {
                    colors: ["#98A1C0"],
                    fontSize: '.6875em',
                    fontFamily: 'Inter, Arial, sans-serif',
                    fontWeight: 400,
                    cssClass: 'apexcharts-xaxis-label',
                },
            }
        },
        yaxis: {
            labels: {
                style: {
                    colors: ["#98A1C0"],
                    fontSize: '.75em',
                    fontFamily: 'Inter, Arial, sans-serif',
                    fontWeight: 400,
                    cssClass: 'apexcharts-yaxis-label',
                },
                formatter: function(val, index) {
                    return "$ " + (+val/1000000).toFixed(1) + " m";
                }
            }
        },
        tooltip: {
            x: {
                format: 'dd MMMM, yyyy'
            },
        },
    };

    let chart = new ApexCharts(el, options);
    chart.render();

    // let yLabels = document.querySelectorAll(".apexcharts-yaxis-label tspan")
    //
    // yLabels.forEach(label => {
    //     let convertedNum =
    //     label.innerHTML = convertedNum
    // })
}

