let nftSelects = document.querySelectorAll(".m-select")

nftSelects.forEach(select => {
    let toggleBtn = select.querySelector(".m-select__field")
    let field = select.querySelector(".m-select__field input")
    let options = select.querySelectorAll(".m-select__option input")

    toggleBtn.onclick = function () {
        select.classList.toggle("_active")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(select);

        if ( ! withinBoundaries ) {
            if(select.classList.contains("_active")){
                select.classList.remove("_active")
            }
        }
    })

    options.forEach(option => {
        setOption()
        option.onchange = function () {
            setOption()
            select.classList.remove("_active")
        }
    })

    function setOption() {
        let selectedOption = select.querySelector(".m-select__option input:checked")

        if(selectedOption){
            field.value = selectedOption.value
        } else{
            field.value = ""
        }
    }
})
