let settingsToggleButtons = document.querySelectorAll("._toggle-settings")
let liquiditySettings = document.querySelector(".liquidity-settings")

if(liquiditySettings){
    settingsToggleButtons.forEach(btn => {
        btn.onclick = function () {
            liquiditySettings.classList.toggle("_active")
            if(window.innerWidth<=576){
                body.classList.toggle("fixed-body")
            }
        }
    })

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(liquiditySettings);

        if ( ! withinBoundaries ) {
            if(liquiditySettings.classList.contains("_active")){
                liquiditySettings.classList.remove("_active")
                body.classList.remove("fixed-body")
            }
        }
    })
}


