let burger = document.querySelector(".burger")
let body = document.querySelector(".m-page")
let switchToLight = document.querySelectorAll(".theme-switch [data-theme='light']")
let switchToDark = document.querySelectorAll(".theme-switch [data-theme='dark']")
let header = document.querySelector(".header")

if (burger) {
    burger.onclick = function() {
        header.classList.toggle("_open")
        body.classList.toggle("fixed-body")
    }
}


setTheme()
function setTheme() {
    if(localStorage.getItem("theme") === "light"){
        body.classList.add("_light")
    } else{
        body.classList.remove("_light")
    }
}


if(switchToLight){
    switchToLight.forEach(btn =>{
        btn.onclick = function (){
            localStorage.setItem("theme","light")
            setTheme()
        }
    })
}
if(switchToDark){
    switchToDark.forEach(btn =>{
        btn.onclick = function (){
            console.log(true)
            localStorage.setItem("theme","dark")
            setTheme()
        }
    })
}
