let farmViewToggles = document.querySelectorAll(".view-toggle__item")
let farm = document.querySelector(".farm")

if(farm){
    farmViewToggles.forEach(btn => {
        btn.onclick = function () {
            if(btn.classList.contains("_set-table-view")){
                farm.classList.remove("_card-view")


            } else{
                farm.classList.add("_card-view")
            }
            farmViewToggles.forEach(btn => {
                btn.classList.remove("_active")
            })
            this.classList.add("_active")
        }
    })
}
