let alertTogglers = document.querySelectorAll("[data-alert]")
let alerts = document.querySelectorAll(".m-alert")

alertTogglers.forEach(el => {
    el.onclick = function () {
        let alertId = el.getAttribute("data-alert")
        openAlert(alertId)
    }
})

function openAlert(alertId) {
    let alert = document.querySelector(`#${alertId}`)
    let closeButtons = alert.querySelectorAll(`._close-alert`)

    alert.classList.add("_active")


    closeButtons.forEach(btn => {
        btn.onclick = ()=> {
            alert.classList.remove("_active")
        }
    })

    setTimeout(()=>{
        alert.classList.remove("_active")
    },8000)
}


