let vestRows = document.querySelectorAll(".vest-table tbody tr")
let poolRows = document.querySelectorAll(".pool-table tbody tr")


vestRows.forEach(row => {
    row.onclick = function(){
        if(window.innerWidth > 576){
            return
        }

        let data = this.getAttribute("data-row")
        data = JSON.parse(data)

        let img = document.querySelector("#vestActionModal .pair__img img")
        let tokenName = document.querySelector("#vestActionModal ._tokenName")
        let tokenDesc = document.querySelector("#vestActionModal ._tokenDesc")
        let positionNum = document.querySelector("#vestActionModal ._positionNum")
        let positionDesc = document.querySelector("#vestActionModal ._positionDesc")
        let vestNum = document.querySelector("#vestActionModal ._vestNum")
        let vestDesc = document.querySelector("#vestActionModal ._vestDesc")

        img.setAttribute("src",data.imgSrc)
        tokenName.innerHTML = data.tokenName
        tokenDesc.innerHTML = data.tokenDesc
        positionNum.innerHTML = data.positionNum
        positionDesc.innerHTML = data.positionDesc
        vestNum.innerHTML = data.vestNum
        vestDesc.innerHTML = data.vestDesc
        openModal("vestActionModal")
    }

})

poolRows.forEach(row => {
    row.onclick = function(){
        if(window.innerWidth > 576){
            return
        }

        let data = this.getAttribute("data-row")
        data = JSON.parse(data)

        let img = document.querySelector("#poolClaimModal ._img1")
        let img2 = document.querySelector("#poolClaimModal ._img2")
        let tokenName = document.querySelector("#poolClaimModal ._tokenName")
        let tokenDesc = document.querySelector("#poolClaimModal ._tokenDesc")
        let positionNum = document.querySelector("#poolClaimModal ._positionNum")
        let positionDesc = document.querySelector("#poolClaimModal ._positionDesc")
        let positionNum2 = document.querySelector("#poolClaimModal ._positionNum2")
        let positionDesc2 = document.querySelector("#poolClaimModal ._positionDesc2")
        let currency = document.querySelector("#poolClaimModal ._currency")
        let price = document.querySelector("#poolClaimModal ._price")

        img.setAttribute("src",data.imgSrc)
        img2.setAttribute("src",data.imgSrc2)
        tokenName.innerHTML = data.tokenName
        tokenDesc.innerHTML = data.tokenDesc
        positionNum.innerHTML = data.positionNum
        positionDesc.innerHTML = data.positionDesc
        positionNum2.innerHTML = data.positionNum2
        positionDesc2.innerHTML = data.positionDesc2
        currency.innerHTML = data.currency
        price.innerHTML = data.price
        openModal("poolClaimModal")
    }

})
