let searchFields = document.querySelectorAll(".search-field")

searchFields.forEach(field => {
    let input = field.querySelector("input")

    let isMultiPlaceholder = input.getAttribute("data-placeholder")
    if(isMultiPlaceholder){
        setPlaceholder()
        window.addEventListener('resize', () => {
            setPlaceholder()
        })
    }

    function setPlaceholder(){
        let placeholder = input.getAttribute("data-placeholder")
        let placeholderMob = input.getAttribute("data-placeholder-mob")
        if(window.innerWidth>576){
            input.setAttribute("placeholder",placeholder)
        }else{
            input.setAttribute("placeholder",placeholderMob)
        }
    }

    input.oninput = function () {
        let val = this.value

        if(val.length > 2){
            field.classList.add("_active")
        } else{
            field.classList.remove("_active")
        }
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(field);

        if ( ! withinBoundaries ) {
            if(field.classList.contains("_active")){
                field.classList.remove("_active")
            }
        }
    })
})
