let filterItems = document.querySelectorAll(".filter-item")

filterItems.forEach(item => {
    let btn = item.querySelector(".filter-item__btn")
    let radios = item.querySelectorAll(".m-radio input")

    radios.forEach(radio => {
        radio.onchange = function () {
            let text = radio.closest(".m-radio").querySelector(".m-radio__text .text16").innerHTML

            console.log(text)


            item.classList.remove("_active")

            if(item.classList.contains("_type2")){
                item.querySelector(".filter-item__btn .text16").innerHTML = text
            }
        }
    })

    btn.onclick = function () {
        item.classList.toggle("_active")
    }
    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(item);

        if ( ! withinBoundaries ) {
            if(item.classList.contains("_active")){
                item.classList.remove("_active")
            }
        }
    })
})
